<template>
  <div class="slider">
    <carousel
      :per-page="1"
      :autoplay="true"
      :autoplayTimeout="1000"
      :navigationEnabled="true"
    >
      <slide>
        <img
          src="data:image/jpeg;base64,"
          alt=""
          class="img"
        />
      </slide>
      <slide>
        <img
          src="data:image/jpeg;base64,"
          class="img"
          alt=""
        />
      </slide>
      <slide>
        <img
          src="data:image/jpeg;base64,"
          class="img"
          @mousedown="down($event)"
          @mouseup="up($event)"
          alt=""
        />
      </slide>
    </carousel>
  </div>
</template>

<script>
import { Carousel, Slide } from "vue-carousel";
export default {
  components: {
    Carousel,
    Slide
  },
  data() {
    return {
      scrollX: 0
    };
  },
  methods: {
    sex() {
      if (Math.abs(this.scrollX) === 0) {
        alert("Real Clicked !!!");
      } else {
        return;
      }
    },
    down(e) {
      this.scrollX = e.screenX;
    },
    up(e) {
      this.scrollX -= e.screenX;

      this.sex();
    }
  }
};
</script>

<style>
.slider {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.img {
  width: 100%;
}
</style>
